export default {
  name: 'File',
  props: {
    type: {
      type: String,
      required: true
    },
    id: {
      type: [Number, String],
      required: true
    },
    name: {
      type: String,
      required: true
    },
    path: {
      type: String,
      default: ''
    },
    link: {
      type: String,
      default: ''
    }
  },
  computed: {
    title: function title() {
      var lastSlashPosition = this.path.lastIndexOf('/');
      var firstSlashPosition = this.path.indexOf('/');
      var path = this.path.substring(firstSlashPosition === 0 ? 1 : 0, lastSlashPosition);
      if (path.length === 0) {
        return '';
      }
      return t('notifications', 'in {path}', {
        path: path
      });
    }
  }
};